const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			imgUrl: '',
			type: '',
			typeText1: '所有额度',
			typeText2: '所有贷款分类',
			list: [],
			currentIndex: 0,
			mask: false,
			quota: '',
			keyShow: [],
			moneyList: [
				{
					title: '所有额度'
				},
				{
					title: '100-5000'
				},
				{
					title: '5000-2万'
				},
				{
					title: '2万-5万'
				},
				{
					title: '5万-10万'
				},
				{
					title: '10万以上'
				}
			],
			typeList: [],
			category_id: '',
			down: '0',
			top: '0',
			vip: '',
			isShow: true,
			isShow1: false
		};
	},
	onLoad(e) {
		console.log(e);
		this.typeText2 = e.title;
		this.category_id = e.id;
		this.imgUrl = helper.imgUrl;
		this.getList();
	},
	methods: {
		goProduct(e) {
			let id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url: `/pages/productDetail?id=${id}`
			});
		},
		// 标签列表显示
		getTap(e) {
			this.type = e;
			if (e == 1) {
				this.keyShow = this.moneyList;
				this.down = 1;
			} else {
				this.keyShow = this.typeList;
				this.top = 1;
				this.getTypeList();
			}
			this.mask = true;
		},
		// 标签选择
		screenList(e) {
			console.log(e, '****');
			this.currentIndex = e.currentTarget.dataset.index;
			if (this.type == 1) {
				this.typeText1 = this.keyShow[this.currentIndex].title;
				this.quota = this.keyShow[this.currentIndex].title;
				this.down = 0;
				// this.typeText2 = '所有贷款分类'
			} else {
				// this.typeText1 = '所有额度'
				this.typeText2 = this.keyShow[this.currentIndex].title;
				this.category_id = e.currentTarget.dataset.id;
				this.top = 0;
			}
			this.list = [];
			this.mask = false;
			this.getList();
		},
		// 隐藏标签列表
		hideModal() {
			this.mask = false;
			this.down == 0;
			this.top == 0;
		},
		getTypeList() {
			uni.request({
				url: `${helper.requestUrl}/holes/categories`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res, '----');
					if (res.data.status_code == 200) {
						this.keyShow = res.data.data;
					} else {
					}
				}
			});
		},
		//获取列表数据
		getList() {
			uni.request({
				url: `${helper.requestUrl}/holes/categories-holes`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data: {
					category_id: this.category_id,
					quota: this.quota
					// page_size: this.page_size,
					// page: this.page
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = this.list.concat(res.data.data);
						if (res.data.data == 0) {
							uni.showToast({
								title: '暂无更多数据',
								icon: 'none'
							});
							this.isShow = true;
							console.log(this.isShow);
							this.isShow1 = false;
						} else {
							this.isShow = false;
							this.isShow1 = false;
						}
					} else if (res.data.status_code == 202) {
						this.vip = res.data.message;
						this.isShow1 = true;
						this.isShow = false;
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
				}
			});
		}
	}
};
